import React, { Component, useCallback, useEffect, useState, useRef } from "react";
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'
import styled from 'styled-components'
import Login from './Login'
import Shoebill from "./Shoebill";

class Admin extends Component {
    state = {
        isLogin: false,
        accessToken: null,
        role: 0,
    }

    loginHandler = () => {
        this.setState({isLogin: !this.state.isLogin})
    }

    setAccessToken = (accessToken) => {
        this.setState({accessToken: accessToken})
    }

    setRole = (role) => {
        this.setState({role: role})
    }


    render () {
        return (
            <div>
                {this.state.isLogin || <Login isLogin={this.state.isLogin} loginHandler={this.loginHandler} setAccessToken={this.setAccessToken} setRole={this.setRole} />}
                {this.state.isLogin && this.state.accessToken && this.state.role && <Shoebill />}


            </div>

        )
    }
}

export default Admin

