import React, { useCallback, useEffect, useState, useRef } from "react";
import io from "socket.io-client"
import axios from 'axios'
import styled from 'styled-components'

function Login(props) {

    const refId = useRef()
    const refPwd = useRef()

    const auth = async() => {
        const options = {
            method: 'post',
            url: 'https://api.cokkirimarket.xyz/user/login',
            data: {
                "email": refId.current.value,
                "password": refPwd.current.value
            }
        }
        const grant = await axios(options)
        console.log(grant)
        if(grant.status === 200){
            props.loginHandler()
            props.setAccessToken(grant.data.accessToken)
            props.setRole(grant.data.userInfo.role)
        }
    }

    return (
        <Main>
            
            <LoginContainer>
                <Header>
                <Label>관리자 로그인</Label>
                <Labe2>Cokkirimarket</Labe2>
                
                </Header>
                <Hr1 />
                
                

                <IdInput ref={refId} />
                <PWDInput ref={refPwd} />
                <Btn1
                content='잘못된 아이디 혹은 비밀번호 입니다.'
                type='error'
                onClick={auth}
                >로그인</Btn1>

            </LoginContainer>
            
            

            
        </Main>

    )
}

const Main = styled.div`
    margin: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50vh;
`

const LoginContainer = styled.div`
//margin: auto;
width: 350px;
-webkit-text-stroke: 0.45px;//폰트 안티앨리어싱;
`

const Hr1 = styled.hr`
    margin: 0 0 30px 0;
`

const Header = styled.div`
    display: flex;
    flex-direction: column;

`

const Label = styled.div`
    font-size: 30px;
    font-family: 'Spoqa Han Sans Neo';
    font-weight: 500;
    color: #666666;
    margin: 10px auto 0 0;
`

const Labe2 = styled.div`
    font-size: 16px;
    font-family: 'Spoqa Han Sans Neo';
    font-weight: 300;
    color: #666666;
    margin: 0 auto 5px 10px;
`

const IdInput = styled.input.attrs({
    type: 'text',
    placeholder: '아이디',
    
})`
display: block;
position: relative;
  overflow: hidden;
  font-family: 'Spoqa Han Sans Neo';
  width: 100%;
  height: 40px;
  margin: 0 0 8px;
  padding: 5px 39px 5px 11px;
  border: solid 1px #dadada;
  background: #fff;
  box-sizing: border-box;
    -webkit-border-image: none;

`

const PWDInput = styled.input.attrs({
    type: 'password',
    placeholder: '비밀번호',
})`
display: block;
position: relative;
  overflow: hidden;
  font-family: 'Spoqa Han Sans Neo';
  width: 100%;
  height: 40px;
  margin: 0 0 8px;
  padding: 5px 39px 5px 11px;
  border: solid 1px #dadada;
  background: #fff;
  box-sizing: border-box;
    -webkit-border-image: none;
    //border-top-left-radius: 20px;
    //border-top-right-radius: 20px;
`

const Btn1 = styled.button`
margin: 15px 0 0 0;
font-size: 18px;
font-family: 'Spoqa Han Sans Neo';
  font-weight: 300;
  line-height: 49px;
  display: block;
  width: 100%;
  height: 49px;
  cursor: pointer;
  text-align: center;
  color: #fff;
  border: none;
  border-radius: 0;
  background-color: #69779F;
`

export default Login