import React, { useCallback, useEffect, useState, useRef } from "react";
import io from "socket.io-client"
import styled from 'styled-components'

const socket = io.connect(`https://api.cokkirimarket.xyz`)
socket.emit('init', { name: "123", message: "123"})

function Shoebill() {

    const [chatArr, setChatArr] = useState([ { name: "123", message: "123"} ]);
    const [chat, setChat] = useState({ name: "", message: "" });
    const messageEnd = useRef()
    const refMessage = useRef()

    useEffect(() => {
      return () => {
        socket.close();
      };
    }, []);
    useEffect( async () => {
      socket.emit("pm2_log_general", { name: chat.name, message: chat.message }); 
      socket.on("server_msg", (message) => {
        setChatArr((chatArr) => chatArr.concat(message));
        scrollToBottom()
      }); //receive message이벤트에 대한 콜백을 등록해줌
    }, []);
    const buttonHandler = useCallback(() => {
      socket.emit("init", { name: chat.name, message: chat.message }); 
      refMessage.current.value = ''
      //버튼을 클릭했을 때 send message이벤트 발생
    }, [chat]);

    const serverlogbtn = () => {
      socket.emit("server_log", { name: chat.name, message: chat.message }); 
    }

    const consolelogbtn = () => {
      socket.emit("pm2_log_general", { name: chat.name, message: chat.message }); 
    }


    const consoleerrlogbtn = () => {
      socket.emit("pm2_log_error", { name: chat.name, message: chat.message }); 
    }


    const changeMessage = useCallback(
      (e) => {
        setChat({ name: chat.name, message: e.target.value });
      },
      [chat]
    );
    const changeName = useCallback(
      (e) => {
        setChat({ name: e.target.value, message: chat.message });
      },
      [chat]
    );
    const scrollToBottom = () => {
        messageEnd.current.scrollIntoView({ behavior: 'smooth' })
    }

    return (
        <ChatUIContatiner className="ChatApp">
      <MessageContainer className="Box">
        <ChatBox className="ChatBox">
          {chatArr.map((ele, index) => (
            <Chat className="Chat" key={`chat-${index}`}>
              <div>{ele.name}</div>
              <div className="ChatLog" ref={messageEnd}>{ele.message}</div>
            </Chat>
          ))}
        </ChatBox>

      </MessageContainer>
      <InputUIContainer>
        <SubmitButton onClick={serverlogbtn}>  서버 접속 로그  </SubmitButton>
        <SubmitButton onClick={consolelogbtn}> 콘솔 로그 </SubmitButton>
        <SubmitButton onClick={consoleerrlogbtn}> 에러 로그 </SubmitButton>
        </InputUIContainer>
    </ChatUIContatiner>
    )

}
const ChatUIContatiner = styled.div`
  width: 95vw;
  height: 95vh;
  margin: 10px auto 0 auto;
  //padding : 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  //align-items: center;
`

const MessageContainer = styled.div`
margin: 20px 0 20px 0;
overflow: hidden;
width:100%;
  height: 100%;
  border-width: 0px;
     border-color: #e5e5e5;
     background-color: #ffffff;
     color: #303030;
     border-style: solid;
     border-radius: 0px;
     box-shadow: 0px 0px 5px rgba(66,66,66,.60);
`

const InputUIContainer = styled.div`
display: flex;
  

  justify-content: center;
  align-items: center;
`

const ChatBox = styled.div`
display: flex;
flex-direction: column;
overflow: auto;
width: 100%;
height: 90%;
padding: 10px;
 display: flex;
 justify-content: center;



`

const Chat = styled.div`
background: rgba( 150, 237, 255, 0.2 );
font-size: 12px;
box-shadow: 0 1px 4px 0 rgba( 31, 38, 135, 0.37 );
backdrop-filter: blur( 4.5px );
-webkit-backdrop-filter: blur( 4.5px );
border-radius: 3px;
border: 1px solid rgba( 255, 255, 255, 0.18 );

margin: 5px 5px 5px 10px;
padding: 5px 15px 5px 15px;
display: flex;
  width: calc(100% - 50px);
`

const InputName = styled.input.attrs({
    placeholder: '이름',
})`
    width: 6em;
    margin: 5px;
     padding: 10px;
     font-size: 16px;
     border-width: 0px;
     border-color: #e5e5e5;
     background-color: #ffffff;
     color: #303030;
     border-style: solid;
     border-radius: 0px;
     box-shadow: 0px 0px 5px rgba(66,66,66,.60);
     text-shadow: 0px 0px 5px rgba(66,66,66,.14);
     :focus {
        outline:none;
     }
`
const InputMessage = styled.input.attrs({
    placeholder: '내용',
})`
    width: 30em;
    margin: 5px;
     padding: 10px;
     font-size: 16px;
     border-width: 0px;
     border-color: #e5e5e5;
     background-color: #ffffff;
     color: #303030;
     border-style: solid;
     border-radius: 0px;
     box-shadow: 0px 0px 5px rgba(66,66,66,.60);
     text-shadow: 0px 0px 5px rgba(66,66,66,.14);
     :focus {
        outline:none;
     }
`

const SubmitButton = styled.div`
    height: 35px;
    width: 100px;
    vertical-align: middle;
    margin: 0 10px 0 10px;
    text-align: center;
    line-height: 30px;
    font-size: 14px;
     border-width: 0px;
     border-color: #e5e5e5;
     background-color: #ffffff;
     color: #303030;
     border-style: solid;
     border-radius: 0px;
     box-shadow: 0px 0px 5px rgba(66,66,66,.60);
     :hover {
        background-color: #dedede;
     }
`

export default Shoebill;